@import "../../scss/variables";

.bg-primary {
  background-color: $primary1 !important;

  &::hover,
  &::focus {
    background-color: $primary1 !important;
  }
}

.bg-primary-transparent {
  background-color: $primary1-transparent;
}

.select2-container--classic {
  .select2-selection--single:focus,
  &.select2-container--open .select2-selection--single,
  .select2-selection--multiple:focus {
    border: 1px solid $primary1;
  }

  &.select2-container--open {
    .select2-selection--multiple {
      border: 1px solid $primary1;
    }

    .select2-dropdown {
      border-color: $primary1;
    }
  }
}

.main-header-center .search-btn {
  background-color: $primary1;
}

.text-primary {
  color: $primary1 !important;

  &:hover,
  :focus {
    color: $primary1 !important;
  }
}

.border-primary {
  border-color: $primary1 !important;
}

.bd-primary {
  border-color: $primary1;
}

.card-item .card-item-icon.card-icon {
  background: $primary1-transparent;
  fill: $primary1;
}

.card-dashboard-calendar .ui-datepicker {
  .ui-datepicker-month {
    color: $primary1;
  }

  .ui-datepicker-calendar .ui-datepicker-today a {
    background-color: $primary1;
    border: 1px solid $primary1;
  }
}

#back-to-top {
  background: $primary1;
}

.ckbox span:after {
  background-color: $primary1;
}

.btn-primary {
  color: $white !important;
  background-color: $primary1 !important;
  border-color: $primary1 !important;

  &:hover,
  &:focus {
    box-shadow: 0 0 0 1px rgba(134, 103, 255, 0.2);
    color: $white;
    background-color: $primary1-hover !important;
    border-color: $primary1-hover !important;
  }

  &.disabled,
  &:disabled {
    color: $white;
    background-color: $primary1;
    border-color: $primary1;
  }

  &:not(:disabled):not(.disabled) {
    &:active,
    &.active {
      color: $white;
      background-color: $primary1-hover;
      border-color: $primary1-hover;
    }
  }
}

.show > .btn-primary.dropdown-toggle {
  color: $white;
  background-color: $primary1-hover;
  border-color: $primary1-hover;
}

.btn-primary:not(:disabled):not(.disabled) {
  &:active:focus,
  &.active:focus {
    box-shadow: 0 0 0 1px rgab(134, 103, 255, 0.2);
  }
}

.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 1px rgab(134, 103, 255, 0.2);
}

.btn-main-primary {
  color: $white;
  background-color: $primary1;
  border-color: $primary1;

  &:hover {
    color: $white;
    background-color: $primary1-hover;
    border-color: $primary1-hover;
  }

  &:focus,
  &.focus {
    box-shadow: 0 0 0 1px rgba(134, 103, 255, 0.3);
  }

  &.disabled,
  &:disabled {
    color: $white;
    background-color: $primary1;
    border-color: $primary1;
  }

  &:not(:disabled):not(.disabled) {
    &:active,
    &.active {
      color: $white;
      background-color: $primary1;
      border-color: $primary1;
    }
  }
}

.show > .btn-main-primary.dropdown-toggle {
  color: $white;
  background-color: $primary1;
  border-color: $primary1;
}

.btn-main-primary:not(:disabled):not(.disabled) {
  &:active:focus,
  &.active:focus {
    box-shadow: 0 0 0 1px rgba(134, 103, 255, 0.3);
  }
}

.show > .btn-main-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 1px rgba(134, 103, 255, 0.3);
}

.btn-outline-primary {
  color: $primary1 !important;
  border-color: $primary1;

  &:hover {
    color: $white !important;
    background-color: $primary1;
    border-color: $primary1;
  }

  &:focus,
  &.focus {
    box-shadow: 0 0 0 1px rgba(134, 103, 255, 0.2);
  }

  &.disabled,
  &:disabled {
    color: $primary1;
    background-color: transparent;
  }

  &:not(:disabled):not(.disabled) {
    &:active,
    &.active {
      color: $white !important;
      background-color: $primary1;
      border-color: $primary1;
    }
  }
}

.show > .btn-outline-primary.dropdown-toggle {
  color: $white !important;
  background-color: $primary1;
  border-color: $primary1;
}

.btn-outline-primary:not(:disabled):not(.disabled) {
  &:active:focus,
  &.active:focus {
    box-shadow: 0 0 0 1px rgba(134, 103, 255, 0.2);
  }
}

.show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 1px rgba(134, 103, 255, 0.2);
}

.badge-primary {
