      color: $primary1;
    }
  }
  .nav-sub-item.active > .nav-sub-link {
    color: $primary1;

    &:before {
      color: $primary1;
    }
  }

  .nav-sub-link {
    &:hover,
    &:focus {
      color: $primary1;
    }
  }
}

@media (min-width: 992px) {
      
.dark-theme .main-navbar.hor-menu .nav-item {
  &.active .nav-link, &:hover .nav-link, &.active .nav-link.with-sub::after, &:hover .nav-link.with-sub::after,
  .nav-sub-item.active>.nav-sub-link:before {
    color: $primary1 !important;
  }
}
