  &.active .nav-link, &:hover .nav-link, &.active .nav-link.with-sub::after, &:hover .nav-link.with-sub::after,
  .nav-sub-item.active>.nav-sub-link:before {
    color: $white !important;
  }
}
  .main-navbar {
    .nav-sub-link {
      &:hover,
      &:focus {
        color: $primary1;
      }
    }

    &.hor-menu .nav-sub-item.active a.active {
      color: $primary1;
    }
  }
}

